@use 'variables' as v;
@use 'mixins' as m;

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: bolder;
    color: v.$azul-obscuro-2;
    text-align: center;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
p {
    color: v.$azul-obscuro-2;
    font-size: 2rem;
}
.nombre-pagina {
    text-align: center;
    margin: 0 0 5rem;
    font-weight: v.$bold;
    @include m.tablet {
        margin: 5rem 0;
    }
}
.descripcion-pagina {
    text-align: center;
}
