/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f6f6f6;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Inclusive Sans", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inclusive Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: bolder;
  color: #046e8f;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #046e8f;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 0 0 5rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .nombre-pagina {
    margin: 5rem 0;
  }
}

.descripcion-pagina {
  text-align: center;
}

.contenedor-app {
  color: #046e8f;
  background-image: url("../img/3.png");
  background-size: cover;
  background-position: center;
  min-height: 100vh;
}
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-app .imagen {
  width: 10rem;
  height: 10rem;
  margin: auto;
  margin-top: 10px;
  background-image: url("../img/confidentLogo.png");
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    margin: auto;
    width: 50%;
    height: 50%;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 4rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* Colores */
  --Color-base: #ffffff;
  --Color-obscuro: #022f40;
  --Color-cyanLight: #38aecc;
  --Color-titulos: #0090c1;
  --Color-cyanDark: #046e8f;
  /* Tamanos texto */
  --Titulo1: 60px;
  --Titulo2: 40px;
  --Texto: 16px;
}

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

header {
  background-color: #333;
  color: #fff;
  padding: 1em;
  text-align: center;
  display: flex;
  justify-content: space-between;
}

nav ul {
  margin-top: 30px;
  list-style: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 10px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
}

section {
  padding: 2em;
}

#banner {
  background-image: url("banner.jpg"); /* Agrega la URL de tu imagen de banner */
  background-size: cover;
  color: #fff;
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
  max-width: 300px;
  margin: 0 auto;
}

form label,
form input {
  margin-bottom: 10px;
}

footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 1em;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.logo-container {
  display: flex;
  align-items: center;
}

.logo-container img {
  width: 50px;
  max-height: 50px; /* Ajusta la altura máxima según tu necesidad */
  margin-right: 10px; /* Espacio entre el logo y el nombre */
}

.Informacion-Ubicacion {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
  background-color: #fff;
}

.info-leftside,
.info-rightside {
  flex: 1;
  padding: 20px;
}

.info-leftside img {
  max-width: 100%;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.info-rightside h2 {
  font-size: 1.8em;
  color: #333;
  margin-bottom: 10px;
}

.info-rightside p {
  font-size: 1.2em;
  color: #666;
  line-height: 1.6;
}

.servicios {
  text-align: center;
  padding: 0;
}

.servicios h2 {
  font-size: 2em;
  color: #333;
  margin-bottom: 30px;
}

.contenedor-principal {
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-cards {
  display: flex;
  justify-content: center;
}

.columna1,
.columna2 {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.card-servicios {
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin: 10px;
  width: 200px;
  height: 200px;
  text-align: center;
  overflow: hidden;
  transition: transform 0.3s ease; /* Movido el transition aquí */
}

.card-servicios:hover {
  transform: scale(1.1);
}

.card-servicios img {
  margin-top: 30px;
  max-width: 60px;
  border-radius: 0%; /* Haciendo la imagen completamente circular */
}

.card-servicios p {
  font-size: 1.2em;
  color: #555;
}

.contacto {
  text-align: center;
  padding: 20px 0;
}

.contacto h2 {
  font-size: 2em;
  color: #333;
  margin-bottom: 20px;
}

.contacto-contenedor-cards {
  display: flex;
  justify-content: center;
}

.contacto-card {
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin: 20px;
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contacto-card img {
  max-width: 100px;
}

.contacto-card img:hover {
  transform: scale(1.1);
  transition: transform 0.3s ease;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
  align-items: left;
}
@media (min-width: 768px) {
  .campo {
    flex-direction: row;
    align-items: center;
  }
}
.campo label {
  flex: 0 0 3rem;
  margin-left: 1rem;
  font-weight: bold;
  font-size: 1.7rem;
}
@media (min-width: 768px) {
  .campo label {
    flex: 0 0 10rem;
  }
}
.campo input {
  flex: 1;
  border: 2px solid #f6f6f6;
  border-radius: 12px;
  padding: 1rem 2rem;
}

.boton {
  background-color: #0090c1;
  padding: 1rem 3rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 12px;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
    margin: 5rem 0;
  }
}
.boton:hover {
  background-color: #006a8e;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    margin-top: 5rem;
    display: flex;
    justify-content: space-around;
  }
}
.acciones a {
  color: #0090c1;
  display: block;
  margin-bottom: 1.25rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  border-radius: 12px;
  text-align: center;
}
.alerta.error {
  background-color: #ed4b4b;
}
.alerta.exito {
  background-color: #31c971;
}